import { PlainUtils } from '../../../../../utils/PlainUtils';
import { BaseFont } from '../../../../../canvas/font/BaseFont';
import { ColsIterator } from '../../../iterator/ColsIterator';

class CellOutRange {

  constructor({
    cells, cols, rows, merges,
  }) {
    this.cells = cells;
    this.cols = cols;
    this.merges = merges;
    this.rows = rows;
  }

  left({
    row, col,
  }) {
    const { cells, cols, rows, merges } = this;
    const rowObject = rows.getOrNew(row);
    const master = cells.getCell(row, col);
    let find = true;
    let leftWidth = cols.getWidth(col - 1);

    // 
    if (rowObject.hasAngleCell()) {
      ColsIterator.getInstance()
        .setBegin(col - 1)
        .setEnd(0)
        .setLoop((i) => {
          // 
          const cell = cells.getCell(row, i);
          if (PlainUtils.isUnDef(cell)) {
            return true;
          }
          // 
          const merge = merges.getFirstIncludes(row, i);
          if (PlainUtils.isNotUnDef(merge)) {
            return true;
          }
          // 
          const { text } = cell;
          if (PlainUtils.isBlank(text)) {
            return true;
          }
          // 
          const { fontAttr } = cell;
          const { direction } = fontAttr;
          // 
          if (direction === BaseFont.TEXT_DIRECTION.ANGLE) {
            const { angle } = fontAttr;
            // angleBar
            if (cell.isAngleBarCell()) {
              if (angle < 0) {
                return true;
              }
            } else {
              // 90
              if (angle === 90 || angle === -90) {
                return true;
              }
              // 
              const { align } = fontAttr;
              if (align !== BaseFont.ALIGN.left && align !== BaseFont.ALIGN.center) {
                return true;
              }
            }
          } else {
            // 
            const { textWrap } = fontAttr;
            if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
              return true;
            }
            // 
            const { align } = fontAttr;
            if (align !== BaseFont.ALIGN.left && align !== BaseFont.ALIGN.center) {
              return true;
            }
          }
          // 
          const width = cells.getCellBoundOutSize(row, i);
          if (width > leftWidth) {
            // master
            // 
            if (PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text)) {
              find = false;
              return false;
            }
          }
          return true;
        })
        .setNext((i) => {
          leftWidth += cols.getWidth(i);
        })
        .execute();
    } else {
      ColsIterator.getInstance()
        .setBegin(col - 1)
        .setEnd(0)
        .setLoop((i) => {
          // 
          const cell = cells.getCell(row, i);
          if (PlainUtils.isUnDef(cell)) {
            return true;
          }
          // 
          const merge = merges.getFirstIncludes(row, i);
          if (PlainUtils.isNotUnDef(merge)) {
            return true;
          }
          // 
          const { text } = cell;
          if (PlainUtils.isBlank(text)) {
            return true;
          }
          // 
          const { fontAttr } = cell;
          const { textWrap } = fontAttr;
          // 
          if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
            return false;
          }
          // 
          const { align } = fontAttr;
          if (align !== BaseFont.ALIGN.left && align !== BaseFont.ALIGN.center) {
            return false;
          }
          // 
          const width = cells.getCellBoundOutSize(row, i);
          if (width > leftWidth) {
            // master
            // 
            if (PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text)) {
              find = false;
            }
          }
          return false;
        })
        .setNext((i) => {
          leftWidth += cols.getWidth(i);
        })
        .execute();
    }
    return find;
  }

  right({
    row, col,
  }) {
    const { cells, cols, merges, rows } = this;
    const { len } = cols;
    const rowObject = rows.getOrNew(row);
    const last = cells.getCell(row, col - 1);
    const master = cells.getCell(row, col);
    let find = true;
    let rightWidth = cols.getWidth(col + 1) + cols.getWidth(col);

    // 
    if (rowObject.hasAngleCell()) {
      ColsIterator.getInstance()
        .setBegin(col + 1)
        .setEnd(len)
        .setLoop((j) => {
          // 
          const cell = cells.getCell(row, j);
          if (PlainUtils.isUnDef(cell)) {
            return true;
          }
          // 
          const merge = merges.getFirstIncludes(row, j);
          if (PlainUtils.isNotUnDef(merge)) {
            return true;
          }
          // 
          const { text } = cell;
          if (PlainUtils.isBlank(text)) {
            return true;
          }
          // 
          const { fontAttr } = cell;
          const { direction } = fontAttr;
          // 
          if (direction === BaseFont.TEXT_DIRECTION.ANGLE) {
            const { angle } = fontAttr;
            // angleBar
            if (cell.isAngleBarCell()) {
              if (angle > 0) {
                return true;
              }
            } else {
              // 90
              if (angle === 90 || angle === -90) {
                return true;
              }
              // 
              const { align } = fontAttr;
              if (align !== BaseFont.ALIGN.right && align !== BaseFont.ALIGN.center) {
                return true;
              }
            }
          } else {
            // 
            const { textWrap } = fontAttr;
            if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
              return true;
            }
            // 
            const { align } = fontAttr;
            if (align !== BaseFont.ALIGN.right && align !== BaseFont.ALIGN.center) {
              return true;
            }
          }
          // 
          const width = cells.getCellBoundOutSize(row, j);
          if (width > rightWidth) {
            // master
            // last
            // 
            const masterBlank = PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text);
            const nextBlank = PlainUtils.isUnDef(last) || PlainUtils.isBlank(last.text);
            if (masterBlank && nextBlank) {
              find = false;
              return false;
            }
          }
          return true;
        })
        .setNext((j) => {
          rightWidth += cols.getWidth(j);
        })
        .execute();
    } else {
      ColsIterator.getInstance()
        .setBegin(col + 1)
        .setEnd(len)
        .setLoop((j) => {
          // 
          const cell = cells.getCell(row, j);
          if (PlainUtils.isUnDef(cell)) {
            return true;
          }
          // 
          const merge = merges.getFirstIncludes(row, j);
          if (PlainUtils.isNotUnDef(merge)) {
            return true;
          }
          // 
          const { text } = cell;
          if (PlainUtils.isBlank(text)) {
            return true;
          }
          // 
          const { fontAttr } = cell;
          const { textWrap } = fontAttr;
          // 
          if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
            return false;
          }
          // 
          const { align } = fontAttr;
          if (align !== BaseFont.ALIGN.right && align !== BaseFont.ALIGN.center) {
            return false;
          }
          // 
          const width = cells.getCellBoundOutSize(row, j);
          if (width > rightWidth) {
            // master
            // last
            // 
            const masterBlank = PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text);
            const nextBlank = PlainUtils.isUnDef(last) || PlainUtils.isBlank(last.text);
            if (masterBlank && nextBlank) {
              find = false;
            }
          }
          return false;
        })
        .setNext((j) => {
          rightWidth += cols.getWidth(j);
        })
        .execute();
    }
    return find;
  }

}

export {
  CellOutRange,
};
